      <div class="main-container">

        <!-- Page header starts -->
        <Navbar page="Recursos Humanos / Salários" />
        <!-- Page header ends -->

        <!-- Content wrapper scroll start -->
        <div class="content-wrapper-scroll">

          <!-- Content wrapper start -->
          <div class="content-wrapper">

            <!-- Row start -->
            <div class="row">
              <div class="col-12">
                <div class="card">
                  <div class="d-flex p-3">
                    <button data-bs-toggle="modal" data-bs-target="#addForm" class="btn btn-primary btn-sm fw-bold d-flex align-items-center p-2 rounded-3 border-0 text-uppercase">
                      <span class="fas fa-plus mx-1 fs-5"></span> Adicionar
                    </button>
                    <!--<button @click="selectRow" data-bs-toggle="modal" data-bs-target="#editForm" class="btn btn-info btn-dark btn-sm mx-1 fw-bold d-flex align-items-center p-2 rounded-3 border-0 text-uppercase">
                      <span class="fas fa-pencil mx-1 fs-5"></span> Editar
                    </button>-->
                    <button @click="selectRow" data-bs-toggle="modal" data-bs-target="#removeForm" class="btn btn-danger btn-sm mx-1 fw-bold d-flex align-items-center p-2 rounded-3 border-0 text-uppercase">
                      <span class="fas fa-trash mx-1 fs-5"></span> Excluir
                    </button>
                    <button @click="openSalarySheet" class="btn btn-success text-white btn-sm mx-1 fw-bold d-flex align-items-center p-2 rounded-3 border-0 text-uppercase">
                      <span class="fas fa-file-excel mx-1 fs-5"></span> Folha Salarial
                    </button>
                  </div>
                  <div class="card-body pt-0">
                     <div class="row">
                        <div class="col-md-3">
                            <label>Selecione o ano letivo</label><br>
                            <select class="form-select" v-model="filter_data.ano_lectivo_id">
                                <option 
                                    v-for="ano in anos" 
                                    :key="ano.id"     
                                    :value="ano.id"
                                >{{ ano.nome }}</option>    
                            </select>
                        </div>
                        <div class="col-md-3">
                            <label></label><br>
                            <button @click="filterData" class="btn btn-info text-uppercase fw-bold">
                                <span class="fas fa-search"></span> Buscar
                            </button>
                        </div>
                     </div>
                      <DataTable 
                        :data="salarios" 
                        :columns="columns" 
                        :options="{ select: true, language: language }"
                        ref="table"
                        class="display table table-bordered table-striped">
                        <thead>
                          <tr>
                            <th>MÊS</th>
                            <th>ESTADO</th>
                          </tr>
                        </thead>
                      </DataTable>
                  </div>
                </div>
              </div>
            </div>
            <!-- Row end -->

            <!-- Modals -->
              <!-- Add Modal -->
              <div class="modal fade" data-bs-backdrop = "static" id="addForm">
                <div class="modal-dialog rounded-0">
                    <div class="modal-content rounded-0">
                    <div class="modal-header">
                        <h4>{{ title }}</h4>
                    </div>
                    <div class="modal-body">
                        <div class="row">
                            <div class="col-md-12 mb-3">
                                <label>Mês</label><br>
                                <select class="form-select" v-model="salario.mes">
                                    <option v-for="mes in meses" :key="mes" :value="mes">{{ mes }}</option>
                                </select>
                            </div>
                            <div class="col-md-12 mb-3">
                                <label>Ano Letivo</label><br>
                                <select class="form-select" v-model="salario.ano_lectivo_id">
                                    <option 
                                        v-for="ano in anos" 
                                        :key="ano.id"     
                                        :value="ano.id"
                                    >{{ ano.nome }}</option>    
                                </select>
                            </div>
                            <div class="col-md-12 mb-3">
                                <label>Estado</label><br>
                                <select class="form-select" v-model="salario.estado">
                                    <option value="Agendados">Agendados</option> 
                                    <option value="Processados">Processados</option> 
                                    <option value="Cancelados">Cancelados</option> 
                                </select>
                            </div>
                        </div>
                    </div>
                    <div class="modal-footer border-0 pt-0">
                        <button class="btn btn-secondary rounded-0" data-bs-dismiss="modal">Fechar</button>
                        <button @click="submitForm" class="btn btn-primary rounded-0">Enviar</button>
                    </div>
                    </div>
                </div>
              </div>

            <!-- Edit Modal -->
            <div class="modal fade" data-bs-backdrop = "static" id="editForm">
              <div class="modal-dialog rounded-0">
                <div class="modal-content rounded-0">
                  <div class="modal-header">
                    <h4>{{ title }}</h4>
                  </div>
                  <div class="modal-body">
                        <div class="row">
                            <div class="col-md-12 mb-3">
                                <label>Mês</label><br>
                                <select class="form-select" v-model="salario.mes">
                                    <option v-for="mes in meses" :key="mes" :value="mes">{{ mes }}</option>
                                </select>
                            </div>
                            <div class="col-md-12 mb-3">
                                <label>Ano Letivo</label><br>
                                <select class="form-select" v-model="salario.ano_lectivo_id">
                                    <option 
                                        v-for="ano in anos" 
                                        :key="ano.id"     
                                        :value="ano.id"
                                    >{{ ano.nome }}</option>    
                                </select>
                            </div>
                            <div class="col-md-12 mb-3">
                                <label>Estado</label><br>
                                <select class="form-select" v-model="salario.estado">
                                    <option value="Agendados">Agendados</option> 
                                    <option value="Processados">Processados</option> 
                                    <option value="Cancelados">Cancelados</option> 
                                </select>
                            </div>
                        </div>
                    </div>
                  <div class="modal-footer border-0 pt-0">
                    <button class="btn btn-secondary rounded-0" data-bs-dismiss="modal">Fechar</button>
                    <button @click="submitForm" class="btn btn-primary rounded-0">Enviar</button>
                  </div>
                </div>
              </div>
            </div>

            <!-- Remove Modal -->
            <div class="modal fade" data-bs-backdrop = "static" id="removeForm">
              <div class="modal-dialog rounded-0">
                <div class="modal-content rounded-0">
                  <div class="modal-header">
                    <h4>{{ title }}</h4>
                  </div>
                  <div class="modal-body">
                    <div class="row">
                      <p>
                        Deseja excluir <b>{{ salario.mes }}</b> definitivamente da base de dados?
                      </p>
                    </div>
                  </div>
                  <div class="modal-footer border-0 pt-0">
                    <button class="btn btn-secondary rounded-0" data-bs-dismiss="modal">Fechar</button>
                    <button @click="deleteForm" class="btn btn-primary rounded-0">Confirmar</button>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <!-- Content wrapper end -->

          <!-- App Footer start -->
          <Footer />
          <!-- App footer end -->

        </div>
        <!-- Content wrapper scroll end -->

      </div>
    </div>
    <!-- Page wrapper end -->
</template>

<script>
import $ from 'jquery';
import '../static';
import Sidebar from '../components/Sidebar.vue';
import Navbar from '../components/Navbar.vue';
import Footer from '../components/Footer.vue';
import { api, language } from '../../../helpers/api';
import DataTable from 'datatables.net-vue3';
import DataTablesCore from 'datatables.net';
import Select from 'datatables.net-select';
import { mapState } from 'vuex';

DataTable.use(DataTablesCore);
DataTable.use(Select);

export default {
  name: "salario_view",
  components: { Sidebar, Navbar, Footer, DataTable },
  data() {
    return {
      title: "Gerenciar Salários",
      salarios: [],
      funcionarios: [],
      anos: [],
      meses: ['Janeiro', 'Fevereiro', 'Março', 'Abril', 'Maio', 'Junho', 'Julho', 'Agosto', 'Setembro', 'Outubro', 'Novembro', 'Dezembro'],
      filter_data: {},
      salario: {},
      columns: [
        { data: 'mes' },
        { data: 'estado' },
      ],
      language: language
    }
  },
  computed: {
    ...mapState(['user'])
  },
  mounted() {
    this.getFuncionarios();
    this.getAnos();
  },
  methods: {
    getAnos() {
      api.get(`/anos/`)
        .then(res => {
          this.anos = res.data;
        })
        .catch(err => {
          console.log(err);
        });
    },
    getFuncionarios() {
      api.get(`/funcionarios/`)
        .then(res => {
          this.funcionarios = res.data;
        })
        .catch(err => {
          console.log(err);
        });
    },
    filterData() {
      api.get(`/salarios/ano/${this.filter_data.ano_lectivo_id}/`)
        .then(res => {
            this.salarios = res.data.length > 0 ? res.data : [];
        })
        .catch(err => {
          console.log(err);
        });
    },
    submitForm() {
      if (this.salario.id > 0) {
        api.put(`/salarios/${this.salario.id}`, this.salario)
          .then(res => {
            if (res.data.title === true) {
              this.$swal.fire({
                title: "Sucesso",
                text: res.data.message,
                icon: "success"
              });
              this.filterData();
              $(".btn-secondary").click();
            } else {
              this.$swal.fire({
                title: "Erro",
                text: "Não foi possível atualizar",
                icon: "error"
              });
            }
          })
          .catch(err => {
            console.log(err);
          });
      } else {
        api.post("/salarios/", { ...this.salario, user_id: this.$store.state.user.id })
          .then(res => {
            if (res.data.title === true) {
              this.$swal.fire({
                title: "Sucesso",
                text: res.data.message,
                icon: "success"
              });
              this.filterData();
              $(".btn-secondary").click();
            } else {
              this.$swal.fire({
                title: "Erro: Não foi possível cadastrar",
                icon: "error"
              });
            }
          })
          .catch(err => {
            console.log(err);
          });
      }
    },
    deleteForm() {
      api.delete(`/salarios/${this.salario.id}`)
        .then(res => {
          if (res.data.message) {
            this.$swal.fire({
              title: "Sucesso",
              text: res.data.message,
              icon: "success"
            });
            this.filterData();
            $(".btn-secondary").click();
            this.salario = {};
          } else {
            this.$swal.fire({
              title: "Erro: Não foi possível excluir",
              icon: "error"
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    selectRow() {
      const table = this.$refs.table.dt;
      const self = this;  

      table.rows({ selected: true }).every(function () {
        const data = this.data();
        self.salario = data;
      });
    },
    openSalarySheet(){
        const table = this.$refs.table.dt;
        const self = this;  

        table.rows({ selected: true }).every(function () {
            const data = this.data();
            self.salario = data;
        });

        this.$router.push(`/salary-sheet-manage/${this.salario.id}/`)
    },
    clearForm() {
      this.salario = {};
    },
  },
}
</script>

<style>
@import 'datatables.net-bs5';
